$raleway: 'Raleway', sans-serif;
$roboto:'Roboto', sans-serif;
$navy: #061e37;

// basic settings 
html {
    font-size: 10px; 
}
* {
    margin: 0;
    padding: 0;
}
.section {
    padding: 10rem 0;
}
.section-header {
    margin: 0 auto;
    font-family: $raleway;
    text-align: center;
    width: 56rem;
}
.section-header__title {
    font-size: 4.5rem;
    font-weight: 600;
    color: $navy;
}
.section-header__suptitle {
    font-size: 2.5rem;
    font-weight: 600;
    color: #b0bec5;
}
.section-header__text {
    margin-top: 1.5rem;
    font-size: 1.5rem;
    line-height: 1.7;
    color: #b0bec5;
}
.button {
    padding: 1.7rem 0;
    color: #fff;
    text-align: center;
    border: none;
    font-family: $raleway;
    font-size: 1.5rem;
    font-weight: 700;
    border-radius: .5rem;
    box-shadow: 0 .5rem .5rem rgba(0,0,0,.15);
    &:focus {
        outline: none;

    }
}
.button_color_light {
    color: $navy;
    background: #fff;
}
.button_color_light:hover {
    background: darken(#fff, 8%);
}
.button_color_dark {
    color: #fff;
    background: $navy;
}
.button_color_dark:hover {
    background: lighten($navy, 8%);
}
.button_color_green {
    color: #fff;
    background: #1de9b6;
}
.button_color_green:hover {
    background: darken(#1de9b6, 8%);
}
.link {
    color: inherit;
    cursor: pointer;
    &:focus {
        outline: none;
    }
    &:hover {
        color: inherit;
        text-decoration: none;
    }
}
ul {
    padding: 0;
    list-style: none;
}
.main {
    padding-top: 5rem;
    padding-bottom: 18.5rem;
    background: url(../img/main/BG.jpg) center center no-repeat;
    background-size: cover;
}
.logo {
    width: 18rem;
}
.logo__img {
    width: 100%;
}
.menu_header {
    display: flex;
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%)
}
.menu__item {
    display: inline-block;
    margin-right: 4.5rem;
}
.menu__item:last-child {
    margin-right: 0;
}
.menu__link {
    color: #fff;
    font-family: $raleway;
    font-weight: 600;
    font-size: 1.5rem;
}
.menu__link_header:hover {
    color: $navy;
}
.button_header {
    padding: 0.2rem 2rem;
    position: absolute;
    top: 50%;
    right: 0;
    transform: translate(-20%, -50%);
    font-size: 1.2rem;
    background-color: inherit;
    border: 0.1rem solid #fff;
    border-radius: .2rem;
    box-shadow: none;
}
.button_header:hover {
    color: #0bb6bd;
    background-color: #fff;
}
.main__content {
    padding-top: 16rem;
    color: #fff;
}
.main__title {
    display: inline;
    font-size: 5.5rem;
    font-family: $raleway;
    font-weight: 800;
    text-transform: uppercase;
}
.main__text {
    margin-top: 1rem;
    margin-bottom: 3.5rem;
    font-family: $raleway;
    font-size: 1.8rem;
    line-height: 1.8;
}
.main__button_buy {
    margin-right: 3rem;
    font-weight: 400;
    width: 15rem;
}
.main__button_try {
    width: 16rem;
}
.main__content-video{
    height: 100%;
}
.video {
    position: relative;
    height: 100%;
    border: .1rem solid $navy;
    border-radius: .5rem;
    overflow: hidden;
    box-shadow: 0 1rem 2rem rgba(0,0,0,.2);
}
.video__content {
    display: block;
    width: 100%;
    height: 100%;
    background: url(../img/main/video.jpg) center center no-repeat;
    background-size: cover;
}
.video__cover {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: #fff;
    opacity: .9;
    z-index: 9;
}
.video__button {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    width: 6rem;
    height: 6rem;
    z-index: 10;
    box-shadow: 0 .5rem 1rem rgba(0,0,0,.2);
}
.button__arrow {
    height: 1.2rem;
}
.video__text {
    position: absolute;
    top: 65%;
    left: 50%;
    transform: translate(-50%, 0);
    font-family: $raleway;
    font-size: 1.2rem;
    font-weight: 800;
    color: $navy;
    z-index: 10;
}
.easy-content {
    margin-top: 9rem;
    display: flex;
    justify-content: space-between;
    font-family: $raleway;
    text-align: center;
}
.easy-content__item {
    width: 35rem;
}
.easy-content__img {
    height: 8rem;
}
.easy-content__title {
    margin-top: 4rem;
    font-size: 2rem;
    font-weight: 600;
    color: $navy;
}
.easy-content__text {
    margin-top: 1rem;
    font-size: 1.5rem;
    color: #78909c;
}
.easy__button {
    display: block;
    margin: 9rem auto 0;
    width: 15rem;
}
.count_row {
    margin: 0;
}
.count__item {
    margin: 0 -1.5rem;
    padding: 5rem 0;
    display: flex;
    justify-content: center;
    font-family: $raleway;
    font-size: 2rem;
    font-weight: 800;
    color: #fff;
}
.count__content {
    margin: 0 auto;
    display: flex;
    justify-content: center;
}
.count__text {
    position: relative;
    display: inline-block;
    margin-left: 2rem;
}
.count__item_color_blue {
    background: #80d8ff;
}
.count__item_color_darkblue {
    background: #82b1ff;
}
.count__item_color_purple {
    background: #b388ff;
}
.count__item_color_pink {
    background: #ea80fc;
}
.accordion {
    margin: 5rem auto 0;
    width: 80rem;
    font-family: $raleway;
    font-size: 1.5rem;
    color: $navy;
}
.accordion__item {
    margin-bottom: 2rem;
    border-radius: .4rem;
    box-shadow: 0 .2rem .5rem rgba(0,0,0,.13);
}
.accordion__item:last-child {
    margin-bottom: 0;
}
.accordion__item_active {
    box-shadow: 0 .5rem 2rem rgba(0,0,0,.1);
}
.accordion__header {
    padding: 1.5rem 2.5rem;
    position: relative;
    cursor: pointer;
}
.accordion__header_active {
    padding: 2rem 2.5rem;
}
.accordion__icon {
    position: absolute;
    top: 50%;
    left: 2.5rem;
    z-index: 1;
    transform: translateY(-50%);
}
.accordion__title {
    margin-left: 4rem;
    font-weight: 700;
}
.accordion__button {
    position: absolute;
    top: 50%;
    right: 2.5rem;
    z-index: 1;
    width: 1.2rem;
    height: 0.6rem;
    background: url(../img/services/arrow_drop.png) center center no-repeat;
    background-size: contain;
    transform: translateY(-50%);
    border: none;
}
.accordion__button:focus {
    outline: none;
}
.accordion__content {
    padding: 0rem 2.5rem 2.5rem;
    display: none;
}
.accordion__content_active {
    display: flex;
}
.accordion__img {
    display: block;
    width: 33%;
    border-radius: .4rem;
}
.accordion__text {
    padding-left: 2.5rem;
    display: block;
    width: 66%;
}
.review {
    padding: 8.5rem 0;
    background: url(../img/review/review-bg.jpg) center center no-repeat;
    background-size: cover;
}
.section-header_review {
    margin: 4rem 0 0 0;
    width: 55rem;
    text-align: left;
}
.section-header__title_review {
    color: #fff;
}
.section-header__text_review {
    color: #fff;
    font-weight: 600;
}
.review__button {
    margin-top: 6rem;
    width: 15rem;
}
.review-slider {
    position: relative;
    margin-left: 7rem;
    color: $navy;
}
.review-slider__button {
    position: absolute;
    left: 50%;
    width: 2.5rem;
    height: 1.5rem;
    transform: translate(-50%, 0);
    border: none;
}
.riview-slider__button:focus {
    outline: none;
}
.review-slider__button_up {
    top: -5rem;
    background: url(../img/review/arrow_up.png);
}
.review-slider__button_down {
    bottom: -5rem;
    background: url(../img/review/arrow_down.png);
}
.review-slider__item {
    position: relative;
    margin-bottom: 7rem;
    padding: 3rem;
    background: #fff;
    font-family: $raleway;
    border-radius: .5rem;
    box-shadow: 0 .5rem 1rem rgba(0,0,0,.15);
}
.review-slider__item:last-child {
    margin-bottom: 0;
}
.review-slider__photo {
    position: absolute;
    top: 50%;
    left: -5rem;
    transform: translate(-100%, -50%);
    border-radius: 50%;
    box-shadow: 0 .5rem 1rem rgba(0,0,0,.15);
}
.review-slider__author {
    font-size: 1.8rem;
    font-weight: 700;
}
.review-slider__text {
    margin-top: 1rem;
    margin-bottom: 0;
    font-size: 1.5rem;
    font-style: italic;
    color: #78909c;
}


.team_content {
    margin: 12rem auto 0;
    width: 65rem;
}
.team-slider {
    position: relative;
    margin: 0 auto;
    width: 46rem;
    display: flex;
    justify-content: space-between;
}
.team-slider__button {
    display: none;
    position: absolute;
    top: 30%;
    width: 3rem;
    height: 4rem;
    box-shadow: 0 0.2rem 0.3rem rgba(0,0,0,0.15);
}
.team-slider__button_left {
    left: 0;
    background: url(../img/team/arrow_left.png) center center no-repeat #0ec0bf;
}
.team-slider__button_right {
    right: 0;
    background: url(../img/team/arrow_right.png) center center no-repeat #0ec0bf;
}
.team-slider__item {
    width: 21.7%;
}
.team-slider__photo {
    width: 100%;
    cursor: pointer;
}
.item__content {
    display: none;
    margin: -1.5rem -110% 0;
    font-family: $raleway;
    text-align: center;
}
.team-slider__name {
    font-size: 1.8rem;
    font-weight: 700;
    color: $navy;
}
.team-slider__text {
    margin-top: 1rem;
    font-size: 1.5rem;
    color: #90a4ae;
}
.socials {
    display: flex;
    justify-content: space-between;
}
.team-slider__social {
    margin: 2.5rem auto 0;
    width: 31%;
}
.team-slider__item_active {
    width: 43.4%;
    .team-slider__photo {
        position: relative;
        border: 2rem solid #fff;
        border-radius: 50%;
        box-shadow: 0 0 1rem rgba(0, 0, 0,.07);
        transform: translate(0, -25%);
    }
    .item__content {
        display: block;
    }
}
.contact {
    padding-top: 5rem;
    background: #fafafa;
}
.section-header__text_dark {
    color: $navy;
}
.contact-form {
    margin: 4rem auto 0;
    width: 60rem;
    font-family: $raleway;
    font-size: 1.4rem;
    font-weight: 500;
    color: $navy;
}
.contact-form_item {
    margin-bottom: 2rem;
    padding: 1.5rem;
    width: 100%;
    background: transparent;
    border: .1rem solid $navy;
    border-radius: .5rem;
    &::placeholder {
        color: $navy;
        opacity: 1;
    }
}
.contact-form_item:focus {
    outline: none;
}
.contact-form__message {
    height: 15rem;
    resize: none;
}
.contact-form__checkbox {
    display: none;
} 
.checkbox__label {
    position: absolute;
    top: 50%;
    left: 1.5rem;
    transform: translate(0, -50%);
    cursor: default;
    font-size: 1.5rem;
}
.checkbox__label:before {
    content: " ";
    display: inline-block;
    margin-right: 1rem;
    width: 1.5rem;
    height: 1.5rem;
    border: .2rem solid $navy;
    border-radius: .2rem;
    vertical-align: middle;
   }
   .contact-form__checkbox:checked + .checkbox__label:before {
     background: url(../img/contact/check.png) center center no-repeat;
     background-size: cover;
    }
     
    .contact-form__checkbox:disabled + .checkbox__label:before {
     background: #eee;
     color: #aaa;
    }
.contact-form__button {
    padding: 1.2rem 0;
    width: 12.7rem;
    font-size: 1.6rem;
    font-weight: 500;
}
.footer {
    background: $navy;
}
.menu_footer {
    padding-right: 1.5rem;
    position: absolute;
    top: 50%;
    right: 0;
    transform: translate(0, -50%)
}
.menu__link_footer:hover {
    color: #838f9b;
}
.footer__social {
    margin-top: 5rem;   
    width: 17rem;
    float: right;
}
.about-us {
    margin-top: 5rem;
    font-family: $raleway;
    font-size: 1.5rem;
    color: #838f9b;
}
.abaut-us_links {
    position: relative;
    padding-right: 4rem;
}
.abaut-us_links:hover {
    color: #fff;
}
.abaut-us_links:last-child {
    margin-right: 0;
}
.abaut-us_links::after {
    content: "";
    display: inline-block;
    position: absolute;
    top: 50%;
    right: 2rem;
    transform: translate(0, -50%);
    width: .4rem;
    height: .4rem;
    background-color: #fff;
    border-radius: 50%;
} 
.abaut-us_links:last-child::after {
    display: none;
} 




@media (min-width: 1550px) {
    .container_big {
        margin: 0 auto;
        padding: 0;
        max-width: 144rem;
    }
    .main__content-header {
        width: 57rem;
    }
    .main__content-video {
        float: right;
        width: 60rem;
    }
    
}

@media (max-width: 1549px) {
    .main {
        padding-bottom: 12rem;
    }
    .main__content {
        padding-top: 10rem;
    }
    .section-header_review {
        width: 100%;
    }
    .review-slider__photo {
        left: -3rem;
    }
}

@media (max-width: 1199px) {
    .section {
        padding: 8rem 0;
    }
    .section-header__title {
        font-size: 3.3rem;
    }
    .section-header__suptitle {
        font-size: 2rem;
    }
    .menu__item {
        margin-right: 2.5rem;
    }
    .main {
        padding-bottom: 10rem;
    }
    .main__content {
        padding-top: 8rem;
    }
    .main__title {
        font-size: 3.7rem;
    }
    .main__text {
        margin-top: 0;
        margin-bottom: 2rem;
        font-size: 1.8rem;
        line-height: 1.5;
    }
    .easy-content {
        margin-top: 7rem;
    }
    .easy-content__item {
        width: 27rem;
    }
    .easy-content__img {
        height: 7rem;
    }
    .easy-content__title {
        font-size: 1.8rem;
    }
    .easy__button {
        margin-top: 6rem;
    }
    .count__item {
        padding: 3rem 0;
    }
    .section-header_review {
    margin: 0 auto;
    width: 55rem;
    text-align: center;
    }
    .review__button {
        margin-top: 3rem;
        margin-bottom: 6rem; 
    }
    .review-slider {
        margin-left: 0rem;
    }    
    .review-slider__button_up {
        top: -3rem;
    }
    .review-slider__button_down {
        bottom: -3rem;
    }
    .review-slider__item {
        margin-bottom: 4rem;
    }
    .review-slider__photo {
        top: 1.2rem;
        left: 4rem;
        width: 5rem;
        transform: translate(0, 0);
        box-shadow: none;
    }
    .review-slider__author {
        margin-left: 8rem;
    }
}

@media (max-width: 991px) {
    .section {
        padding: 6rem 0;
    }
    .main {
        padding-top: 4rem;
        padding-bottom: 8rem;
    }
    .logo {
        width: 15rem;
    }
    .menu_header {
        transform: translate(-42%, -50%);
    }
    .main__content {
        padding-top: 6rem;
        text-align: center;
    }
    .main__title {
        font-size: 3rem;
    }
    .main__text {
        font-size: 1.6rem;
    }
    .main__content-video {
        margin-top: 3rem;
    }
    .video__button {
        width: 4rem;
        height: 4rem;
    }
    .button__arrow {
        margin-top: -1.6rem;
    }
    .easy-content__item {
        width: 20rem;
    }
    .section-header__title {
        font-size: 2.7rem;
    }
    .accordion__img {
        width: auto;
    }
    .contact-form__button {
        margin-top: 2.5rem;
    }
    .accordion {
        width: 90%;
    }    
}

@media (max-width: 767px) {
    .body_lock {
        overflow: hidden;
    }
    .section {
        padding: 4rem 0;
    }
    .section-header {
        width: 90%;
    }
    .button {
        padding: 1.2rem;
    }
    .main {
        padding-top: 0;
    }
    .header {
        padding: 1.5rem 0;
        position: fixed;
        display: flex;
        z-index: 100;
        width: 100%;
        background: #0ec0bf;
    }
    .menu_header {
        display: block;
        padding-top: 3rem;
        padding-bottom: 6rem;
        padding-left: 10%;
        position: fixed;
        top: -100%;
        left: 0;
        width: 100%;
        height: 100%;
        overflow: auto;
        transform: translate(0, 0);
        background: #0ec0bf;
        transition: all .3s linear;
    }
    .menu_header_active {
        top: 5.5rem;
    }
    .menu__item {
        display: block;
        margin-bottom: 2rem;
    }
    .menu__link_header {
        font-size: 2rem;
    }
    .burger {
        position: absolute;
        top: 50%;
        right: 0;
        transform: translate(0, -50%);
        width: 2rem;
        height: 2rem;
        background: url(../img/main/menu.png) center center no-repeat;
        background-size: cover;
        transition: all .3s linear;
    }
    .burger_active {
        background: url(../img/main/close.png) center center no-repeat;
        background-size: cover;
    }
    .button_header {
        padding: 0.2rem 2rem;
    }
    .main__content {
        padding-top: 8rem;
    }
    .button__arrow {
        margin-top: -.6rem;
    }
    .easy-content {
        margin-top: 4rem;
    }    
    .easy-content__item {
        width: 15rem;
    }
    .easy-content__img {
        height: 5.5rem;
    }
    .easy-content__title {
        margin-top: 2rem;
        font-size: 1.6em;
    }
    .easy-content__text {
        margin-top: 1rem;
        font-size: 1.3rem;
    }
    .easy__button {
        margin-top: 3rem;
    }
    .count__item {
        padding: 1.5rem 0;
    }
    .accordion {
        font-size: 1.4rem;
    }
    .accordion__item_active {
        box-shadow: 0 0.5rem 1rem rgba(0,0,0,0.1);
    }
    .accordion__content {
        padding: 0 2rem 1.5rem;
    }
    .accordion__img {
        display: none;
    }
    .accordion__text {
        display: block;
        padding: 0;
        width: 100%;
    }
    .review-slider__item {
        margin-bottom: 2rem;
    }
    .team_content {
        width: auto;
    }
    .item__content {
        margin: -1.5rem -80% 0
    }
    .contact-form {
        width: 80%;
    }
    .menu_footer {
        display: none;
    }
    .about-us {
        margin-top: 1.5rem;
    }
    .footer__social {
        margin-top: 2rem;
        width: 20rem;
        float: left;
    }
}

@media (max-width: 575px) {
    .menu_header_active {
        top: 4.1rem;
    }
    .section {
        padding: 3rem 0;
    }
    .section-header {
        text-align: left;
    }
    .section-header__title {
        font-size: 2.3rem;
    }
    .section-header__suptitle {
        font-size: 1.7rem;
    }
    .section-header__text {
        margin-top: 0;
    }
    .button {
        font-size: 1.2rem;
    }
    .header {
        padding: 0.8rem 0;
    }
    .button_header {
        display: none;
    }
    .main__content {
        text-align: left;
    }
    .main__title {
        font-size: 2.5rem;
    }
    .main__button_buy {
        width: 12.5rem;
    }
    .main__button_try {
        width: 12.5rem;
    }

    .easy-content {
        margin-top: 2rem;
        flex-wrap: wrap;
    }
    .easy-content__item {
        margin-top: 1.5rem;
        width: 100%;
    }
    .count__content {
        margin: 0;
    }
    .count__item {
        padding-left: 3rem;
        justify-content: start;
    }

    .accordion {
        margin-top: 2.5rem;
        width: 100%;
    } 

    .review-slider__item:last-child {
        display: none;
    }



    .team_content {
        margin: 6rem auto 0;
    }
    .team-slider {
        display: block;
        width: 100%;
    }
    .team-slider__item {
        display: none;
    }
    .team-slider__item_active {
        display: block;
        width: 100%;
        .team-slider__photo {
            display: block;
            margin: 0 auto;
            width: 15rem;
            border: 1rem solid #fff;
        }
    }
    .item__content {
        margin: 0;
    }
    .team-slider__social {
        width: 23rem;
    }

    .contact-form {
        margin-top: 2rem;
        width: 90%;
    }
    .contact-form_item {
        margin-bottom: 1rem;
        padding: .5rem 1.5rem;
    }
    .contact-form__message {
        height: 8rem;
    }
    .abaut-us_links {
